.mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.blur {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(37, 37, 38, 0.5);
  filter: blur(5px);
}
.dialog {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0 12px;
  background-color: #252526;
  box-shadow: #000000 0 5px 8px;
}
.dialog h1 {
  display: block;
  height: 36px;
  line-height: 36px;
  margin: 0 -12px 12px;
  padding: 0 12px;
  color: #e7e7e7;
  font-size: 11px;
  border-bottom: 1px solid rgba(128, 128, 128, 0.35);
  text-transform: uppercase;
}
.dialog .content {
  flex-grow: 1;
}
.dialog footer {
  margin: 20px 0;
  text-align: right;
}
.dialog footer .button {
  display: inline-block;
  line-height: 16px;
  margin-left: 4px;
  padding: 0 5px;
  color: #fff;
  font-size: 12px;
  background-color: #464646;
  border: 1px solid #545454;
  cursor: pointer;
}
.dialog footer .button.highlight {
  background-color: #478e4b;
  border: 1px solid #519a55;
}



