html,
body,
#app-container {
  width: 100%;
  height: 100%;
  margin: 0;
}
#app-container {
  position: relative;
  background-color: #1e1e1e;
}
* {
  font-family: -apple-system, BlinkMacSystemFont, Segoe WPC, Segoe UI, HelveticaNeue-Light, Ubuntu, Droid Sans, sans-serif;
  font-weight: normal;
  font-size: 12px;
  color: #cccccc;
}
main {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
header {
  display: flex;
  height: 32px;
  line-height: 32px;
  padding: 0 8px;
}
header label {
  display: block;
  padding: 0 8px;
  font-family: Segoe UI;
  font-size: 11px;
  color: #bbbbbb;
  text-transform: uppercase;
}
input {
  box-sizing: border-box;
  display: block;
  height: 24px;
  line-height: 16px;
  padding: 4px;
  border: none;
  background-color: #3c3c3c;
  color: #cccccc;
}
input:focus {
  outline-color: rgba(14, 99, 156, 0.6);
  outline-offset: 0;
}
.drag-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
}
aside {
  box-sizing: border-box;
  position: absolute;
  top: 0;
  bottom: 0;
  padding: 0 16px;
  background-color: #252526;
}
aside.panel-right {
  right: 0;
}
aside.panel-left {
  left: 0;
}
aside h1 {
  display: block;
  height: 36px;
  line-height: 36px;
  margin: 0;
  font-family: Segoe UI;
  font-size: 11px;
  color: #bbbbbb;
  text-transform: uppercase;
  user-select: none;
  cursor: default;
}
select {
  height: 20px;
  margin-top: 6px;
  background-color: #3c3c3c;
  color: #f0f0f0;
  border-color: #3c3c3c;
}
select:focus {
  outline-color: rgba(14, 99, 156, 0.6);
  outline-offset: 0;
}
.url-bar {
  display: flex;
  flex-grow: 1;
  padding: 0 8px;
}
#content {
  position: absolute;
  top: 32px;
  right: 0;
  bottom: 0;
  left: 0;
}
h4 {
  display: block;
  height: 18px;
  line-height: 18px;
  color: #cccccc;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  padding: 4px 0 0;
  margin: 0;
  font-family: Segoe UI;
  font-size: 11px;
  font-weight: 400;
  text-transform: lowercase;
}
textarea {
  box-sizing: border-box;
  display: block;
  width: 100%;
  height: 24px;
  max-height: 120px;
  border: none;
  background-color: #3c3c3c;
  color: #cccccc;
  resize: vertical;
}
textarea:focus {
  outline-color: rgba(14, 99, 156, 0.6);
  outline-offset: 0;
}



